body {
  background: #0c0d4b;
}

header {
  justify-content: center;
  align-items: center;
  display: flex;
}

.logo {
  width: 600px;
  height: 200px;
}

main {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  text-align: center;
  text-transform: uppercase;
  background-color: #daa520;
  border: none;
  border-radius: 8px;
  padding: 15px 32px;
  font-family: Beaufort for LOL, sans-serif;
  font-size: 16px;
  font-style: italic;
  font-weight: 700;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 8px 16px #0003, 0 6px 20px #00000030;
}

#cards-grid {
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 30px;
  width: 100%;
  margin: 40px;
  display: grid;
}

.card {
  border-radius: 8px;
  flex-direction: column;
  max-width: 308px;
  display: flex;
}

.card img {
  border-radius: 8px 8px 0 0;
  width: 100%;
  height: 100%;
}

.card:hover img {
  animation: 1s forwards scale-card;
  display: inline-block;
}

@keyframes scale-card {
  100% {
    transform: scale(1.1);
  }
}

#name {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #b48713;
  border-radius: 0 0 8px 8px;
  justify-content: center;
  align-items: center;
  height: 30px;
  padding: 20px;
  font-family: Beaufort for LOL, sans-serif;
  font-style: italic;
  font-weight: 700;
  display: flex;
}

#champ-image {
  overflow: hidden;
}
